<script>
  import publicFn from '../../../untils/Public.js';
  import AddReservation from './addReservation.vue';
  import $ from 'jquery'
  // 日期插件
  // import '../../../static/js/bootstrap-datepicker.zh-CN.min.js'
  import '../../../../static/css/daterangepicker.css'
  //import '../../../../static/js/moment.js'
  import '../../../../static/js/daterangepicker.js'
  import {mapActions,mapGetters,mapState} from 'vuex'
    export default {
        name: 'ReservationChart',
      data:function(){
        return{
          //阅读协协议
          Checd:true,
          //倒计时
          timeOutS:{},
          //患者id
          usereId:null,
          //病历号
          caseNo:null,
          //下单信息
          //患者id
          pantionId:0,
          //专家ID\
          expertId:null,
          //医生id,
          doctorId:1,
          //订单失效显示
          invalid:false,
          timeForOder:null,
          nameText:"",
          rues:1,
          listData:{
             list1:{
               rues:true,
               alerts:true,
               inputName:"",
               six:"男",
               inputAge:"",
               inputFrendName:"",
               inputIdCard:"",
               inputPhone:"",
               inputPhonebyfrend:""
             },
            list2:{
              bot:false,
              rues:true,
              alerts:true,
              auxiliary:"",
              ChiefComplaint:"",
              MedicalHistoryNow:"",
              MedicalHistoryOf:"",
              MedicalHistoryBySelf:"",
              contagionHistory:"",
              allergyHistory:"",
              familyHistory:"",
              inspect:"",
              auxiliaryInspect:"",
              imgList:["","",""]
            },
            list3:{
              alerts:false,
              time:"",
              status:""
            }
          },
          //患者医院id
          hospitalId:null,
          goPay:null,
          tyoe:0
        }
      },
      mounted:function() {
        document.body.scrollTop =0;
        var id=this.$route.query.id;
        this.id=id;
        var id=this.$route.query.userId;
        this.tyoe=this.$route.query.tyoe;
        this.usereId=id;
        this.tmieOut();
        var id =this.$route.query.type;
        this.$store.commit("ucliFn","");
        this.$store.commit("postRecodeList",null);
        this.$store.commit("pushOderChart",null);
      },
      methods: {
        clickChecd:function (parm) {
          if(parm==true){
            this.Checd=false;
            return
          }else if(parm==false){
            this.Checd=true;
            return
          }

        },
        save1: function (a) {
          //编辑资料
          if (a==3) {
            this.$store.commit('ucliClereFn',-1);
          }
        },
        sixs: function (six) {
          this.listData.list1.six = six;
          this.verification.sixText = "";
        },
        //去付款
        goCertOder: function () {
          if(!this.Checd){
            this.warning("请完阅读并同意协整协议");
            return;
          }
           if(this.goPay){
             this.bind(this.goPay);
           }else{
             this.warning("填写协诊患者病情")
           }
        },
        //返回
        back:function(a){
          if(!a){
            if(this.$route.query.tyoe==1){
              this.$router.push({ name: 'AssistOrders', query: {type:"assisOders"} })
            }else {
              this.$router.push({name: "Expert"})
            }
          }else{
            if(this.$route.query.tyoe==1){
              this.$router.push({ name: 'AssistOrders', query: {type:"assisOders"} })
            }else {
              window.history.back()
            }
          }
        },
        //倒计时
        tmieOut:function(){
          var id =this.id;
          var url='/api/assist-order/assistOrders/'+id;
          var token=localStorage.getItem('access_token');
          this.$http.get(url,{
            headers:{
              Authorization: 'bearer '+token
            }
          }).then(function(res){
            res=res.body;
            //倒计时
            var startTime=res.createdWhen;
            var now=res.now;
            this.goPay=res.recordId;
            var time=120;//多久
            var that=this;
             this.timeForOder= setInterval(function(){
               if(that && that._isDestroyed){ //_isDestroyed 组件是否被销毁
                 return;
               }
              var times= publicFn.publicLo.tmieOut(startTime,time,now);
              now += 1000;
              that.timeOutS=times;
              if( times.timeM<=0){
                clearInterval(that.timeForOder);
                that.warning("订单已失效");
                that.invalid=true;
              }
            },1000);

          });
        },
        open:function(text) {
          this.$message(text);
        },
        //绑定病历
        bind:function(parm){
          var url='/api/assist-order/assistOrders/'+this.id+"?recordId="+parm.recordId+'&description='+parm.description
          this.$http.put(url,{
          }).then(function (res) {
            var res = res.body;
            this.$router.push({name: "CertOder",query: { id:this.id,userId:this.usereId,type:1}});
            this.goPay=null
          }).catch(function(res){
            this.warning("病历绑定失败，请完病例后重试")
          });
        },
        certOderCent:function(){
          window.open("../../../../static/html/GuidelinesForConsultation.html");
//          this.$router.push({name:"certOderCent"})
        },
        success:function(text) {
          this.$message({
            message: text,
            type: 'success'
          });
        },
        warning:function(text){
          this.$message({
            message: text,
            type: 'warning'
          });
        },
      },
      computed: {
        ucli: function() {
          this.goPay= this.$store.state.ucli;
          return this.goPay
        },
        ucliClereData:function () {
          var data=this.$store.state.ucliClere;
          if(data==-1){
            this.listData.list1.alerts=false
          }
          if(data==1){
            this.listData.list1.alerts=true
          }
          return 1
        },
      },
      components: {
          AddReservation:AddReservation
      }
}

</script>



